	module.exports = {"data":{"markdownRemark":{"html":"<p>The <code>charCodeAt()</code> method returns the numeric Unicode value of the character at the given index (except for unicode codepoints > 0x10000).</p>\n<h2>Syntax</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>str.charCodeAt(index)</code></pre>\n      </div>\n<h3>Parameters</h3>\n<p><strong>index</strong></p>\n<p>An integer greater than or equal to 0 and less than the length of the string; if it is not a number, it defaults to 0.</p>\n<p><a href='https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/String/charCodeAt' target='_blank' rel='nofollow'>MDN link</a> | <a href='https://msdn.microsoft.com/en-us/LIBRary/hza4d04f%28v=vs.94%29.aspx' target='_blank' rel='nofollow'>MSDN link</a></p>\n<h2>Description</h2>\n<p>Note that <code>charCodeAt()</code> will always return a value that is less than 65536. This is because the higher code points are represented by a pair of (lower valued) “surrogate” pseudo-characters which are used to comprise the real character. Because of this, in order to examine or reproduce the full character for individual characters of value 65536 and above, for such characters, it is necessary to retrieve not only <code>charCodeAt(i)</code>, but also <code>charCodeAt(i+1)</code> (as if examining/reproducing a string with two letters). See example 2 and 3 below.</p>\n<p><code>charCodeAt()</code> returns <code>NaN</code> if the given <code>index</code> is less than 0 or is equal to or greater than the length of the string.</p>\n<h2>Examples</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>'ABC'.charCodeAt(0); // returns 65\n\nvar str = \"ABCDEFGHIJKLMNOPQRSTUVWXYZ\"; \ndocument.write(str.charCodeAt(str.length - 1));\n\n// Output: 90</code></pre>\n      </div>","fields":{"slug":"/javascript/standard-objects/string/string-prototype-charcodeat/"},"frontmatter":{"title":"String.prototype.charCodeAt"}}},"pathContext":{"slug":"/javascript/standard-objects/string/string-prototype-charcodeat/"}}

